import '../App.css';
import {useState, useEffect, useRef} from 'react';
import {InView} from 'react-intersection-observer';
import wallpaper from '../media/images/wallpaper.jpg'
import TypeIt from "typeit-react";
import backgroundSong from '../media/audio/background_song.mp3'
import autosize from "autosize/dist/autosize";

function Home(props) {
    // const {scrollCoverage, setScrollCoverage} = useState();
    // const {lookDuration, setLookDuration} = useState(0);
    let scrollCoverage = 0, lookDuration = 0, lastIntervalId, lastScrollTop = 0;

    const [auth, setAuth] = useState(false)
    // const {passwordInput, setPasswordInput} = useState("")

    const typeItOptions = {cursor: false, lifeLike: true, waitUntilVisible: true}

    const scrollWrapper = useRef(null)
    const inputField = useRef(null)
    const audioElement = useRef(null)

    const passwords = ["ثروقروقتي", "شروقروقتي", "ثروقروقتى", "شروقروقتى", "ووي", "ووى", "وتيني", "وتينى", "wuee", "shroux", "شروقروقة", "شروقروقه", "ثروقروقة", "ثروقروقه", "شروقروق", "ثروقروق"]

    let infoPack = useRef({
        scrollCoverage: scrollCoverage,
        scrollHeight: 0,
        clientHeight: 0,
        stops: [],
        clicks: []
    })

    const stopWatch = () => setInterval(() => {
        lookDuration += 100
        // console.log(">>> Interval is running <<<")
        infoPack.current.scrollHeight = scrollWrapper.current.scrollHeight
        infoPack.current.clientHeight = scrollWrapper.current.clientHeight
    }, 100)

    useEffect(() => {
        console.log(">>> StopWatch initialized <<<")
        // if(auth) {
        //     lastIntervalId = stopWatch()
        // }

        // console.log(props.socket)
        infoPack.current.resourceURL = window.location.pathname
        infoPack.current.scrollHeight = scrollWrapper.current.scrollHeight
        infoPack.current.clientHeight = scrollWrapper.current.clientHeight
        // props.socket.emit("update_user_reactions", infoPack.current)

    }, [scrollWrapper.current])

    useEffect(()=>{
        autosize(inputField.current)
    }, [inputField.current])


    return !auth ? (
        <div className="font-sans" ref={scrollWrapper}>
            <div className="px-8 py-4 h-[100vh] align-middle font-bold">
                <label style={{direction: "rtl"}} htmlFor="password"
                       className="block mb-2 text-1xl font-medium text-gray-900 dark:text-white">الباسوورد دلعك:</label>

                <input onChange={(event)=>{
                    console.log(event)
                    infoPack.current = {
                        ...infoPack.current,
                        passwordProvided: event.target.value
                    }
                    // props.socket.emit("update_user_reactions", infoPack.current)
                    if(passwords.includes(event.target.value)) {
                        setAuth(true)
                    }
                }} style={{direction: "rtl"}} type="password" id="password" className="mb-5 block w-full p-4 text-1xl text-gray-900 border border-gray-300 rounded-lg bg-gray-50 sm:text-md focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" />
                {/*<button onClick={()=>{*/}
                {/*    */}
                {/*}} style={{direction: "rtl"}} type="submit"*/}
                {/*        className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">دخلني يا سيدي أنا بطاطا بين رجليك</button>*/}
            </div>
        </div>
    ) : (
        <div className="font-sans inset-0" ref={scrollWrapper} onScroll={event => {

            let target = event.target;

            clearInterval(lastIntervalId)

            let scrolledTo = target.scrollTop / (target.scrollHeight - target.clientHeight)

            scrollCoverage = target.scrollTop - lastScrollTop > 0 ? target.scrollTop / (target.scrollHeight - target.clientHeight) : scrollCoverage

            console.log(`The user stopped scrolling for ${lookDuration / 1000}s`)
            console.log(`The scroll coverage is ${scrollCoverage}`)

            infoPack.current.scrollCoverage = scrollCoverage

            if (lookDuration) {
                infoPack.current.stops.push({
                    y: scrolledTo,
                    duration: lookDuration
                })
            }

            // props.socket.emit('update_user_reactions', infoPack.current)

            lookDuration = 0
            lastIntervalId = stopWatch()
            lastScrollTop = target.scrollTop > lastScrollTop ? target.scrollTop : lastScrollTop
        }} onClick={(event) => {
            infoPack.current.clicks.push({
                x: event.pageX,
                y: event.pageY,
                scrollTop: scrollWrapper.current.scrollTop
            })
            // props.socket.emit('update_user_reactions', infoPack.current)
            console.log(infoPack.current)
        }} style={{
            // position: 'fixed',
            // height: '100%',
            // width: '100%',
            overflowY: 'auto'
        }}>
            <div className="bg-black h-full text-white">
                <div className="min-h-screen h-full pb-[200px]">
                    <div className="m-auto flex text-right container items-center px-8 py-4 text-2xl leading-[3rem]">
                        <div className="w-full" style={{direction: 'rtl'}}>
                            بغض النظر عن السبب اللي خلاكي تفتحي الويبسايت، سواء كان صدفة، فضول، أو حتى اشتياق، بس عايز أقولك إني عند وعدي في إنك هتوبي من القرف اللي انت فيه ده أولاً، وثانياً وأخيراً من آخر مرة فاكرها لما مسحتيني وقولتلك امسحيني وحلفت لك إنك مش بتحبيني، كنت مستني منك انك تتمسكي بي وانك تكوني مصرة انك عايزاني، وطبعاً أنا مبدأتش يجيلي الإحساس ده غير لما لاقيتك لا بتسمعي كلامي ولا بتقدري مشاعري ولا بتقدري غيرتي ولا كأن فيه وعد بينا وبتكدبي علي أكثر من اللازم، ف طبيعي تكون ديه ردة فعلي، وطبعاً انت ما شاء الله أكدتيلي حبك المزيف لما بكل سهولة مسحتيني وبكل سهولة رخصتي من نفسك وبكل سهولة روحتي لغيري، هو ده الحب التمام هو ده الإخلاص الحقيقي براڤو، بس عايز أقولك الحقيقي شكراً على وجع القلب والانهزام والكسر اللي عيشتهولي لمدة أكثر من 4 شهور، أنا بتأسف لنفسي إن فعلاً والله كنت بحبك حب حقيقي، وكنت متمسك بيكي وأنا حتى قعدت أصالح فيكي، بس خلي ف علمك أنا عملت كدة عشان مكنتش أعرف حقيقتك، كنت مفكرك نقية كنت مفكرك بتحبيني بجد، مكنتش أعرف إنك وسخة وقذرة إنك طول الفترة ديه كنتي بتكلمي واحد غيري أو حتى كنت لسة بتحاولي معاه. أنا قبل ما اعترفلك بحبي كنت عاطيكي الفرصة إنك تنهي كل شيء من البداية والله كل شيء كان هينتهي فعلاً ومكنش هيبقى فيه زعل وأنا فعلاً كنت بنسحب لما مسحتيني أول مرة خالص، ومكنتيش هتسببيلي الكسر اللي سببتهولي، وعايز أعرفك أنا مش بمنِّ عليكي في أي حاجة عملتهالك أو أي حاجة مادية قدمتهالك، لكن أنا بمنِّ عليكي في مشاعري الحقيقية اللي قدمتهالك، رسوماتي اللي انت شايفة إني بذلك بيها ديه تمنها كان مشاعري، وتمنها كان وقت ضيعته عشانك ع حساب دراستي وحياتي مع إنها أقسم بالله كانت أقل حاجة ممكن أعملهالك لو كنتي إنسانة نقية زي ما تخيلت، وكوني جيتلك أكثر من مرة لحد عندك عشان أصالحك وأكتشف ف الآخر وساختك، ووقتي برضه اللي ضيعته في أرقام تليفون عشان أصالحك وفي الآخر جزائي الخيانة، وكل ده مش شايفة إنك ظلماني، لو كنتي عرفتيني وساختك من البداية ومن قبل ما أصالحك وتعترفيلي إن فيه حد تاني وزي ما قبل ما تمسحيني برضه سألتك واديتك فرصة تانية ننهي كل شيء بشياكة وقولتيلي لا وبتاع وانت بتتخيل حاجات مش حقيقية، لو كنت صارحتيني كنت هحترمك، وبعد كل ده حتى محاولتيش تصالحيني أو حتى تعتذري ع اللي عملتيه وتوضحي موقفك، عرفتي بقى ذنبك؟ عرفتي ليه انت بنت وسخة؟ عرفتي ليه أنا دلوقتي بكرهك؟ عموماً شايفة كل ده أقسم بالله إن حنيتي وطيبة قلبي ما يسمحوليش بالحالة اللي أنا فيها ديه ووالله أنا أكثر إنسان متسامح في الدنيا، بس للأسف إيه فعلاً اللي يخليني أسامحك بعد كل ده، فكري يمكن هتعملي حاجة أسامحك بسببها، وعموماً أنا متاح ع واتساب بس، ومش هسمح بأي شرمطة وكدب تاني بعد كدة، وأقسم بالله لتتوبي
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Home;
